"use server";

import { Currency } from "@/types/currency";
import { CurrencyFormData } from "../validation/currency";
import { ApiError, destroy, GenericResponse, get, post, put } from "./baseApi";
import { CURRENCY_ENDPOINT } from "./endpoints";

export const getCurrencies = async (): Promise<GenericResponse<Currency[]>> => {
  try {
    return await get(`${CURRENCY_ENDPOINT}`);
  } catch (err) {
    console.log(err);
    const error = err as ApiError;
    return {
      success: false,
      data: [] as Currency[],
      message: error.message,
    };
  }
};

export const createCurrency = async (
  body: CurrencyFormData
): Promise<GenericResponse<Currency[]>> => {
  try {
    return await post(`${CURRENCY_ENDPOINT}`, body);
  } catch (err) {
    console.log(err);
    const error = err as ApiError;
    return {
      success: false,
      data: [] as Currency[],
      message: error.message,
    };
  }
};

export const updateCurrency = async ({
  body,
  id,
}: {
  body: CurrencyFormData;
  id: string;
}): Promise<GenericResponse<Currency[]>> => {
  try {
    return await put(`${CURRENCY_ENDPOINT}`, body, id);
  } catch (err) {
    console.log(err);
    const error = err as ApiError;
    return {
      success: false,
      data: [] as Currency[],
      message: error.message,
    };
  }
};

export const deleteCurrency = async ({
  id,
}: Pick<Currency, "id">): Promise<GenericResponse<string>> => {
  try {
    return await destroy(`${CURRENCY_ENDPOINT}`, id);
  } catch (err) {
    console.log(err);
    const error = err as ApiError;
    return {
      success: false,
      data: "",
      message: error.message,
    };
  }
};
