"use server";

import { Client, ClientType } from "@/types/client";
import { ApiError } from "next/dist/server/api-utils";
import { ClientFormSchema } from "../validation/client";
import { destroy, GenericResponse, get, post, put } from "./baseApi";
import { CLIENT_ENDPOINT, CLIENT_TYPE_ENDPOINT } from "./endpoints";

export const getClients = async (): Promise<GenericResponse<Client[]>> => {
  try {
    return await get(CLIENT_ENDPOINT);
  } catch (err) {
    console.log(err);
    const error = err as ApiError;
    return {
      success: false,
      data: [] as Client[],
      message: error.message,
    };
  }
};

export const getClientTypes = async (): Promise<
  GenericResponse<ClientType[]>
> => {
  try {
    return await get(`${CLIENT_TYPE_ENDPOINT}`);
  } catch (err) {
    console.log(err);
    const error = err as ApiError;
    return {
      success: false,
      data: [] as ClientType[],
      message: error.message,
    };
  }
};

export const deleteClient = async (
  id: string
): Promise<GenericResponse<string>> => {
  try {
    return await destroy(CLIENT_ENDPOINT, id);
  } catch (err) {
    const error = err as ApiError;
    return { success: false, data: "", message: error.message };
  }
};

export const createClient = async (
  client: ClientFormSchema
): Promise<GenericResponse<Client>> => {
  try {
    return await post(CLIENT_ENDPOINT, client);
  } catch (err) {
    const error = err as ApiError;
    return { success: false, data: {} as Client, message: error.message };
  }
};

export const updateClient = async ({
  client,
  clientId,
}: {
  client: ClientFormSchema;
  clientId: string;
}): Promise<GenericResponse<Client>> => {
  try {
    return await put(CLIENT_ENDPOINT, client, clientId);
  } catch (err) {
    const error = err as ApiError;
    return { success: false, data: {} as Client, message: error.message };
  }
};
