"use server";

import { EnumResponse } from "@/types/base";
import { Job, JobFilters, JobType, PagedJobs } from "@/types/job";
import { JobFormSchema } from "../validation/job";
import {
  ApiError,
  buildQueryString,
  GenericResponse,
  get,
  post,
  put,
} from "./baseApi";
import {
  JOB_DEADLINE_ENDPOINT,
  JOB_DELIVERY_TYPES_ENDPOINT,
  JOB_ENDPOINT,
  JOB_PAGED_WITH_FILTER_ENDPOINT,
  JOB_STATUS_ENDPOINT,
  JOB_TYPE_ENDPOINT,
  JOB_WORKFLOW_STATUS_ENDPOINT,
} from "./endpoints";

export const getPagedJobs = async (
  filters: JobFilters
): Promise<GenericResponse<PagedJobs>> => {
  try {
    const query = await buildQueryString(filters);
    return await get(`${JOB_PAGED_WITH_FILTER_ENDPOINT}?${query}`);
  } catch (err) {
    console.log(err);
    const error = err as ApiError;
    return {
      success: false,
      data: {} as PagedJobs,
      message: error.message,
    };
  }
};

export const createJob = async (
  job: JobFormSchema
): Promise<GenericResponse<Job>> => {
  try {
    return await post(`${JOB_ENDPOINT}`, job);
  } catch (err) {
    console.log(err);
    const error = err as ApiError;
    return {
      success: false,
      data: {} as Job,
      message: error.message,
    };
  }
};

export const updateJob = async ({
  job,
  id,
}: {
  job: JobFormSchema;
  id: string;
}): Promise<GenericResponse<Job>> => {
  try {
    return await put(`${JOB_ENDPOINT}`, job, id);
  } catch (err) {
    console.log(err);
    const error = err as ApiError;
    return {
      success: false,
      data: {} as Job,
      message: error.message,
    };
  }
};

